<template>
    <div class="contact content">
        <div class="header">
            <img src="/img/img_tel.svg" alt="" />
            <h1>CONTACT</h1>
            <p class="message">お問い合わせはこちらから</p>
        </div>
        <ContactInputList
            v-show="stepNumber === 1"
            @update="updateForm"
        ></ContactInputList>
        <ContactConfirm
            v-if="stepNumber === 2"
            v-bind:form="form"
        ></ContactConfirm>
        <button @click="backStep" v-show="stepNumber != 1" class="back">Back</button>
    </div>
</template>

<script>
import ContactInputList from "@/components/ContactInput.vue";
import ContactConfirm from "@/components/ContactConfirm.vue";

export default {
    name: "Contact",
    components: {
        ContactInputList,
        ContactConfirm,
    },
    data() {
        return {
            stepNumber: 1,
            form: {
                namearea: null,
                textboxarea: null,
            },
        };
    },
    methods: {
        updateForm: function (formData) {
            Object.assign(this.form, formData);
            this.stepNumber = 2;
        },
        backStep: function () {
            this.stepNumber--;
        },
        nextStep: function () {
            this.stepNumber++;
        },
    },
};
</script>
